document.addEventListener("DOMContentLoaded", () => {
  const sections = document.querySelectorAll("section");
  const navLinks = document.querySelectorAll(".home-link");

  navLinks.forEach((link) => {
    link.addEventListener("click", (e) => {
      e.preventDefault(); // Prevent default anchor click behavior

      // Get the target section
      const targetId = e.target.getAttribute("href");
      const targetSection = document.querySelector(targetId);

      // Hide all sections and show the target section
      sections.forEach((section) => {
        if (section === targetSection) {
          section.classList.remove("hidden");
          section.scrollIntoView({ behavior: "smooth" }); // Smooth scrolling
          setTimeout(() => {
            section.scrollIntoView({ behavior: "smooth" });
          }, 100);
        } else {
          section.classList.add("hidden");
        }
      });
    });
  });

  let navbar = document.querySelector(".navbar");
  let closei = document.querySelector(".hamberg1");

  let hambergclick = false;
  if (hamberg && navbar) {
    hamberg.addEventListener("click", () => {
      hambergclick = true;
      // console.log("bj")
      // hamberg.remove()
      // navbar.classList.remove('hide')

      if (hambergclick) {
        // navbar.classList.remove('activate');
        hamberg.style.visibility = "hidden";
        closei.style.visibility = "visible";

        // navbar.classList.remove('hide')

        // icon.append(close)
        navbar.classList.toggle("activate");
      }
    });
  }
  let closeicon = false;
  if (closei) {
    closei.addEventListener("click", () => {
      closeicon = true;
      // console.log("bj")
      // hamberg.remove()
      if (closeicon) {
        // navbar.classList.remove('activate');
        hamberg.style.visibility = "visible";

        closei.style.visibility = "hidden";
        // icon.append(close)
        navbar.classList.toggle("activate");
        // navbar.classList.add('hide')
      }
    });
  }
  if (navbar) {
    navbar.addEventListener("click", (e) => {
      // console.log(window.innerWidth)
      if (
        e.target.id === "I" ||
        (e.target.className === "home-link" && window.innerWidth <= 1524)
      ) {
        // navbar.classList.add('hide')
        navbar.classList.toggle("activate");

        hamberg.style.visibility = "visible";

        closei.style.visibility = "hidden";
        console.log(e.target.id, e.target.className, window.innerWidth);
      }
    });
  }
  // let dynamictext=document.querySelector("#dynamicText")
  // let words=["a Developer",  "an Innovator"];

  // let wordindex=0
  // let charindex=0
  // let isdelete=false;

  // function typeeffext(){
  //     let curword=words[wordindex]
  //     if(isdelete){
  //         charindex--;
  //         dynamictext.textContent=curword.slice(0,charindex)
  //     }
  //     else{
  //         charindex++
  //         dynamictext.textContent=curword.slice(0,charindex)

  //     }
  //     let typingSpeed = isdelete ? 100 : 150;
  //     if (isdelete && charindex === 0) {
  //         isdelete = false;
  //         wordindex = (wordindex + 1) % words.length; // Move to the next word
  //       }

  //       setTimeout(typeeffext, typingSpeed);
  //     }

  //     typeeffext();

  const details = [
    { key: "Name:", value: "Prajwal P S" },
    { key: "Email:", value: "prajwalberpadka@gmail.com" },
    { key: "Phone:", value: "+91 8296080682" },
    { key: "City:", value: "Sullia, Karnataka" },
    { key: "Degree:", value: "Bachelor's" },
    { key: "Age:", value: "19" },
    { key: "Freelance:", value: "Available" },
  ];

  const profileElement = document.getElementById("profile");
  let currentLine = 0;
  let currentKeyIndex = 0;
  let currentValueIndex = 0;

  function typeLetter() {
    if (currentLine < details.length) {
      const line = details[currentLine];

      if (currentKeyIndex < line.key.length) {
        profileElement.innerHTML += `<span class="key">${line.key[currentKeyIndex]}</span>`;
        currentKeyIndex++;

        setTimeout(typeLetter, 100);
      } else if (currentValueIndex < line.value.length) {
        if (currentValueIndex === 0) {
          profileElement.innerHTML += `<span class="specialchar">"</span>`;
        }
        profileElement.innerHTML += ` <span class="value">${line.value[currentValueIndex]}</span>`;

        currentValueIndex++;
        if (currentValueIndex === line.value.length) {
          profileElement.innerHTML += `<span class="specialchar">"</span>`;
        }
        setTimeout(typeLetter, 100);
      } else {
        profileElement.innerHTML += '<span class="specialchar">,</span>';

        profileElement.innerHTML += "<br>";
        currentLine++;
        if (currentLine == 7)
          profileElement.innerHTML += '<div class="specialchar">};</div>';
        currentKeyIndex = 0;
        currentValueIndex = 0;

        setTimeout(typeLetter, 1000);
      }
    }
    // else{
    //     setTimeout(() => {
    //         profileElement.innerHTML = "";
    //         currentLine = 0;
    //         typeLetter();
    //       }, 2000);
    // }
  }

  // Start typing effect
  typeLetter();
});
